import React, { ReactNode, ReactElement, ReactFragment } from 'react';
import { mergeReactNode } from './mergeReactNode';

export type IRenderReactNode<T = undefined> = ReactNode | ((t: T) => Exclude<ReactNode, ReactFragment>) | ((t: T) => (s: ReactElement) => ReactNode);

export const renderReactNode = <T>(
  render: IRenderReactNode<T> | undefined,
  sourceNode: ReactNode,
  source: T,
  depth: number = 2,
): ReactNode => {

  if (typeof render === 'function') {
    const res = render(source);
    if (typeof res === 'function') {
      return res(sourceNode);
    } else {
      return mergeReactNode(res, sourceNode, depth);
    }
  } else {
    return mergeReactNode(render, sourceNode, depth);
  }
}